.goku {
position: fixed;
bottom: 0;
right: 0;
margin: 10px;
z-index: -1;
}
img.logo {
animation: spin 1s cubic-bezier(0, 0, 1, 1) infinite;
max-width: 50px;
}
@keyframes spin{
from {
transform: rotateY(0deg);
}
to {
transform: rotateY(-360deg);
}
}
a {
text-decoration: none;
color: gold;
}
a:active {
color: hsla(0, 0%, 100%, 1);
}
li {
  list-style: decimal;
}
.letter-space {
    letter-spacing: .5rem;
    text-transform: uppercase;
}
.extra-small {
    margin: 0;
    font-size: .75rem!important;
}
.ctr {
  display: flex;
  background-color: #213058;
}