
html {
font-family: sans-serif;
background-color: hsla(231, 37%, 4%, 1);
color: hsla(0, 0%, 100%, 1);
}
body {
display: flex;
flex-direction: column;
flex: 1 1 100%;
margin: 0;
line-height: 160%;
min-height: 100vh;
background: linear-gradient( to bottom, rgba(0, 20, 70, 1) 0%, rgba(0,0,0,0) 70%);
}
img {
display: inline-block;
border: 0;
max-width: 100%;
height: auto;
}
